.bullet {
  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: center;
  justify-content: center;
}

.bullet--chessboard {
  display: flex;
  flex-direction: row;
  padding: 10px 10px 0px 10px;
  height: 650px;
  width: 650px;
  margin: auto;
}

.bullet--info--container {
  display: flex;
  flex-direction: row;
  align-items: start;
}

.bullet--info--1 {
  display: flex;
  flex-direction: column;
  margin-top: 80px;
  border: 3px solid #1d3557;
  background-color: #a8dadc;
  height: 500px;
  width: 350px;
  justify-content: center;
  box-shadow: 4px 26px 58px 1px rgba(29, 53, 87, 1),
    0px 5px 14px 0px rgba(0, 0, 0, 0.18);
}

.bullet--info--2 {
  display: flex;
  flex-direction: column;
  border: 3px solid #1d3557;
  margin-top: 80px;
  height: 500px;
  width: 350px;
  background-color: #a8dadc;
  box-shadow: 4px 26px 58px 1px rgba(29, 53, 87, 1),
    0px 5px 14px 0px rgba(0, 0, 0, 0.18);
}

.bullet--info--3 {
  display: flex;
  flex-direction: column;
  margin: auto;
  height: 70px;
  width: 300px;
  background-color: #e63946;
  border-radius: 10px;
}

.bullet--container {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.bullet--item1,
.bullet--item2,
.bullet--item3,
.bullet--item4,
.bullet--item5,
.bullet--item6,
.bullet--item7 {
  margin: auto;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
}

.bullet--item7 {
  color: #64d86b;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: black;
  background-color: #64d86b;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  border-radius: 8px;
  padding: 5px;
  border: 2px solid black;
  width: 300px;
}

.bullet--solution--button {
  width: 280px;
  padding: auto;
  font-size: 40px;
  background: #1d3557;
  background-image: -webkit-linear-gradient(top, #1d3557, #0f4a9e);
  background-image: -moz-linear-gradient(top, #1d3557, #0f4a9e);
  background-image: -ms-linear-gradient(top, #1d3557, #0f4a9e);
  background-image: -o-linear-gradient(top, #1d3557, #0f4a9e);
  background-image: -webkit-gradient(to bottom, #1d3557, #0f4a9e);
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  border-radius: 10px;
  height: 80px;
  line-height: 50px;
  color: #f1faee;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  font-weight: 100;
  margin: 20px;
  margin-top: -50px;
  -webkit-box-shadow: 1px 1px 20px 0 #000000;
  -moz-box-shadow: 1px 1px 20px 0 #000000;
  box-shadow: 1px 1px 20px 0 #000000;
  border: outset #0059a0 10px;
  text-decoration: none;
  display: inline-block;
  cursor: pointer;
  text-align: center;
}

.bullet--solution--button:hover {
  border: inset #f1faee 10px;
  background: #e63946;
  background-image: -webkit-linear-gradient(top, #0f4a9e, #1d3557);
  background-image: -moz-linear-gradient(top, #0f4a9e, #1d3557);
  background-image: -ms-linear-gradient(top, #0f4a9e, #1d3557);
  background-image: -o-linear-gradient(top, #0f4a9e, #1d3557);
  background-image: -webkit-gradient(to bottom, #0f4a9e, #1d3557);
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  border-radius: 10px;
  text-decoration: none;
}

.bullet--start--button {
  width: 100px;
  font-size: 25px;
  margin: auto;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  background: #1d3557;
  background-image: -webkit-linear-gradient(top, #1d3557, #0f4a9e);
  background-image: -moz-linear-gradient(top, #1d3557, #0f4a9e);
  background-image: -ms-linear-gradient(top, #1d3557, #0f4a9e);
  background-image: -o-linear-gradient(top, #1d3557, #0f4a9e);
  background-image: -webkit-gradient(to bottom, #1d3557, #0f4a9e);
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  border-radius: 10px;
  height: 80px;
  line-height: 50px;
  color: #f1faee;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  font-weight: 100;
  -webkit-box-shadow: 1px 1px 20px 0 #000000;
  -moz-box-shadow: 1px 1px 20px 0 #000000;
  box-shadow: 1px 1px 20px 0 #000000;
  border: outset #0059a0 10px;
  text-decoration: none;
  display: inline-block;
  cursor: pointer;
  text-align: center;
}

.bullet--start--button:hover {
  border: inset #f1faee 10px;
  background: #0f4a9e;
  background-image: -webkit-linear-gradient(top, #0f4a9e, #1d3557);
  background-image: -moz-linear-gradient(top, #0f4a9e, #1d3557);
  background-image: -ms-linear-gradient(top, #0f4a9e, #1d3557);
  background-image: -o-linear-gradient(top, #0f4a9e, #1d3557);
  background-image: -webkit-gradient(to bottom, #0f4a9e, #1d3557);
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  border-radius: 10px;
  text-decoration: none;
}

.bullet--title {
  font-size: 50px;
  font-weight: 500;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  margin: auto;
}

.bullet--title:hover {
  text-decoration: underline;
  text-decoration-color: #e63946;
  cursor: context-menu;
}
