.links--ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  background-color: #1d3557;
  width: 100%;
  max-height: 100%;
  max-width: 100%;
}

.navbar--link {
  float: right;
  color: #f1faee;
  border: none;
  text-align: center;
  text-decoration: none;
  font-size: 30px;
  cursor: pointer;
  padding: 15px 30px 15px 30px;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  margin: auto;
}

.navbar--link--home {
  float: left;
  color: #f1faee;
  border: none;
  text-align: center;
  text-decoration: none;
  font-size: 40px;
  font-weight: 700;
  cursor: pointer;
  padding: 5px;
  margin: auto;
  margin-left: 3em;
  letter-spacing: 2px;
  font-family: "Roboto", sans-serif;
  text-transform: uppercase;
  text-shadow: 1px 1px 0px #e63946;
}

a:link,
a:visited {
  color: #f1faee;
  text-decoration: none;
  display: block;
}

.navbar--link:hover {
  background-color: #e63946;
}

.navbar--link:active {
  color: #f1faee;
  background-color: #a8dadc;
}
