body {
  background-color: #457b9d;
  background-image: url("data:image/svg+xml,%3Csvg width='60' height='60' viewBox='0 0 60 60' xmlns='http://www.w3.org/2000/svg'%3E%3Cg fill='none' fill-rule='evenodd'%3E%3Cg fill='%231d3557' fill-opacity='0.4'%3E%3Cpath d='M36 34v-4h-2v4h-4v2h4v4h2v-4h4v-2h-4zm0-30V0h-2v4h-4v2h4v4h2V6h4V4h-4zM6 34v-4H4v4H0v2h4v4h2v-4h4v-2H6zM6 4V0H4v4H0v2h4v4h2V6h4V4H6z'/%3E%3C/g%3E%3C/g%3E%3C/svg%3E");
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0px 100px 20px 100px;
}

.buttons-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin: 20px;
  justify-content: center;
}

.title {
  position: static;
  padding: 40px 0px 0px 0px;
  margin: auto;
  font-weight: bold;
  letter-spacing: 2px;
  font-size: 150px;
  font-weight: 600;
  font-family: "Roboto", sans-serif;
  color: #f1faee;
  text-transform: uppercase;
  text-shadow: 1px 1px 0px #e63946, 1px 2px 0px #e63946, 1px 3px 0px #e63946,
    1px 4px 0px #a8dadc, 1px 5px 0px #a8dadc, 1px 6px 0px #a8dadc,
    1px 10px 5px rgba(16, 16, 16, 0.5), 1px 15px 10px rgba(16, 16, 16, 0.4),
    1px 20px 30px rgba(16, 16, 16, 0.3), 1px 25px 50px rgba(16, 16, 16, 0.2);
}

.caption {
  position: static;
  margin: auto;
  font-size: 35px;
  letter-spacing: 1px;
  font-weight: 400;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  color: #f1faee;
  text-shadow: 1px 1px 0px #e63946;
}

.mainmenu--button {
  background: #1d3557;
  background-image: -webkit-linear-gradient(top, #1d3557, #0f4a9e);
  background-image: -moz-linear-gradient(top, #1d3557, #0f4a9e);
  background-image: -ms-linear-gradient(top, #1d3557, #0f4a9e);
  background-image: -o-linear-gradient(top, #1d3557, #0f4a9e);
  background-image: -webkit-gradient(to bottom, #1d3557, #0f4a9e);
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  border-radius: 10px;
  height: 80px;
  line-height: 50px;
  color: #f1faee;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  width: 300px;
  font-size: 30px;
  font-weight: 100;
  padding: auto;
  margin: 20px;
  -webkit-box-shadow: 1px 1px 20px 0 #000000;
  -moz-box-shadow: 1px 1px 20px 0 #000000;
  box-shadow: 1px 1px 20px 0 #000000;
  border: outset #0059a0 10px;
  text-decoration: none;
  display: inline-block;
  cursor: pointer;
  text-align: center;
}

.mainmenu--button:hover {
  border: inset #a8dadc 10px;
  background: #0f4a9e;
  background-image: -webkit-linear-gradient(top, #0f4a9e, #1d3557);
  background-image: -moz-linear-gradient(top, #0f4a9e, #1d3557);
  background-image: -ms-linear-gradient(top, #0f4a9e, #1d3557);
  background-image: -o-linear-gradient(top, #0f4a9e, #1d3557);
  background-image: -webkit-gradient(to bottom, #0f4a9e, #1d3557);
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  border-radius: 10px;
  text-decoration: none;
}
