.play-list {
  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: center;
  justify-content: center;
  max-width: 100%;
}

.table-container {
  height: 550px;
  max-width: 100%;
  overflow-y: auto;
  margin-bottom: 20px;
  border: 2px solid #000000;
}

#puzzleTable {
  width: 700px;
  border-collapse: collapse;
  background-color: #a8dadc;
}

#puzzleTable th,
td {
  font-size: 24px;
  font-weight: 500;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  width: auto;
  border: 2px solid rgb(255, 255, 255);
  text-align: center;
}

::-webkit-scrollbar {
  width: 10px;
  height: 50px;
}

::-webkit-scrollbar-thumb {
  background-color: grey;
  border-radius: 10px;
}

::-webkit-scrollbar-track {
  background-color: #f1faee;
  border-radius: 10px;
}

button {
  margin: auto;
  margin-bottom: 10px;
  margin-top: 10px;
  padding: 5px;
  width: auto;
  height: auto;
  background-color: #1d3557;
}

button:hover {
  background: #e63946;
}

.play-title {
  margin: auto;
  padding: 5px;
  font-size: 100px;
  position: static;
  font-weight: bold;
  letter-spacing: 2px;
  font-size: 80px;
  font-weight: 600;
  font-family: "Roboto", sans-serif;
  color: #f1faee;
  text-transform: uppercase;
  text-shadow: 1px 1px 0px #e63946, 1px 2px 0px #e63946, 1px 3px 0px #e63946,
    1px 4px 0px #a8dadc, 1px 5px 0px #a8dadc, 1px 6px 0px #a8dadc,
    1px 10px 5px rgba(16, 16, 16, 0.5), 1px 15px 10px rgba(16, 16, 16, 0.4),
    1px 20px 30px rgba(16, 16, 16, 0.3), 1px 25px 50px rgba(16, 16, 16, 0.2);
}

.refresh-button {
  width: 200px;
  font-size: 25px;
  margin: 15px;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  background: #1d3557;
  background-image: -webkit-linear-gradient(top, #1d3557, #0f4a9e);
  background-image: -moz-linear-gradient(top, #1d3557, #0f4a9e);
  background-image: -ms-linear-gradient(top, #1d3557, #0f4a9e);
  background-image: -o-linear-gradient(top, #1d3557, #0f4a9e);
  background-image: -webkit-gradient(to bottom, #1d3557, #0f4a9e);
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  border-radius: 10px;
  height: 80px;
  line-height: 50px;
  color: #f1faee;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  font-weight: 100;
  -webkit-box-shadow: 1px 1px 20px 0 #000000;
  -moz-box-shadow: 1px 1px 20px 0 #000000;
  box-shadow: 1px 1px 20px 0 #000000;
  border: outset #0059a0 10px;
  text-decoration: none;
  display: inline-block;
  cursor: pointer;
  text-align: center;
}

.refresh-button:hover {
  border: inset #f1faee 10px;
  background: #0f4a9e;
  background-image: -webkit-linear-gradient(top, #0f4a9e, #1d3557);
  background-image: -moz-linear-gradient(top, #0f4a9e, #1d3557);
  background-image: -ms-linear-gradient(top, #0f4a9e, #1d3557);
  background-image: -o-linear-gradient(top, #0f4a9e, #1d3557);
  background-image: -webkit-gradient(to bottom, #0f4a9e, #1d3557);
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  border-radius: 10px;
  text-decoration: none;
}
